import React from 'react'

function NewBatchReportText() {
    return (
        <div>
            <p>Batch reports are now easy to find with the search features implemented. Once you find the report you want you can click on import PropTypes from 'prop-types'
            for a very easy to read report.</p>
            <p> People without any knowledge of the process can insatntly see if the time and viscosity is within specification. Stoppages are highlighted 
                with an explanation in plain English. The back end is also set up for operators to add reasons why a certain issue could happen. Meaning the more
                the system is used the more accurate the stoppage data becomes. 
            </p>
        </div>
    )
}

export default NewBatchReportText
